body {
  font-family: 'Open Sans', sans-serif;
  background-color: #74d680;
}

h1 {
  text-align: center;
}

.globe-wrapper {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  cursor: all-scroll;
  height: 200px;
  width: 200px;
}

.globe-wrapper .globe {
  background-color: rgba(255, 255, 255, 0.15);
  width: 100%;
  height: 100%;
  border-radius: 100%;
  border: white solid 1px;
  overflow: hidden;
  position: relative;
  z-index: -1;
}

.globe-wrapper .globe img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
}

.globe-wrapper .globe #particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
}

.globe-wrapper .stand {
  width: 180px;
  height: 40px;
  background-color: black;
  margin: -20px auto;
  color: white;
  text-align: center;
  line-height: 40px;
  border-radius: 4px;
  border: 1px solid #383838;
}

.shake-text-wrapper {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 240px;
}

.shake-text-wrapper h2 {
  text-align: center;
}

.shake-text-wrapper h2.shaking {
  animation: shake-horizontal 1.5s infinite;
}

